body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #80ED80;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.boxbody {
    height: 500px;
    width: 400px;
    color: blue;
    background-color: #BF7BAA;
    margin: auto;
    position: absolute;
    left: 0;
    top: 100px;
    right: 0;
    bottom: 0;
    border-radius: 30px;
}

.list {
    margin: 50px 0;
    background-color: #997F6F;
    height: 400px;
}
.add {
    height: 30px;
    width: 30px;
}

.addicon {
    position: fixed;
    bottom: 10px;
    right: 10px;
}
.addmodal {
    width: 343px;
    height: 105px;
    position: absolute;
    margin: auto;
    right: 0px;
    top: 8%;
    left: 0px;
    background-color: aqua;
    border-radius: 30px;
}

.head {
    display: block;
    font-size: 20px;
    color: green;
}

.text {
    margin-top: 10px;
    width: 250px;
    height: 28px;
    border-radius: 5px;
    padding-right: 0px;
    background-color: dodgerblue;
}

.addbutton {
    display: block;
    margin: 10px auto;
}
.todolist {
    height: 40px;
}

.todolist:hover .bin {
    display: block;
  }

.bin {
    float: left;
    margin-left: 10px;
    margin-top: 10px;
    display: none;
}

.task {
    margin-left: 10px;
    margin-top: 5px;
    float: left;
}
